import { styled } from 'styled-components';

// The widest viewport width at which the bleed remains at 16px. Above this
// point, the content will remain at 640px wide and the bleed will grow
const maxFullBleedWidth = 640 + 16 * 2 - 1;

export const Wrapper = styled.div`
  @media (max-width: ${maxFullBleedWidth}px) {
    --bleed: ${({ theme }) => theme.dimension.bleed}px;
    --content-width: calc(100vw - (var(--bleed) * 2));
    --margin: calc((100vw - var(--content-width)) / -2);

    width: 100vw;
    box-sizing: border-box;
    margin-right: var(--margin);
    margin-left: var(--margin);
  }
`;
