import { FC } from 'react';

import Tag from '#components/Tag';

import { Wrapper, Title } from './RelatedContent.styles';
import { RelatedContentProps } from './RelatedContent.types';

/**
 * Provides a container for related content which typically appears inline with
 * long-form content
 */
const RelatedContent: FC<RelatedContentProps> = ({
  children,
  className,
  title,
}) => {
  return (
    <Wrapper className={className}>
      <Title>
        <Tag isExpanded={true}>{title}</Tag>
      </Title>
      {children}
    </Wrapper>
  );
};

export default RelatedContent;
